import { R3BoundTarget } from '@angular/compiler';
import { Component } from '@angular/core';
import { RevelDigitalService } from './revel-digital.service';

declare var gadgets: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  prefs;
  title = 'clock-greets';
  time;
  hours;
  msg;
  link;

  constructor(public client: RevelDigitalService) {

    client.getDeviceTime().then((res) => {
      console.log('device time #1: ' + res);
    });

    // client.getDeviceTime(new Date()).then((res) => {
    //   console.log('device time #2: ' + res);
    // });

    client.getDeviceKey().then((res) => {
      console.log('device key: ' + res);
    });

    client.callback('test');

    // if (typeof gadgets !== undefined) {
    //   this.prefs = new gadgets.Prefs();
    //   console.log('rdKey pref: ' + this.prefs.getString('rdKey'));
    // }

    setInterval(() => {
      this.update();
    }, 1000);
  }

  update() {

    this.client.getDeviceTime().then((res) => {
      console.log('device time #1: ' + res);

      this.time = new Date(res);

      this.hours = this.time.getHours();

      if (this.hours < 10) {
        this.msg = 'Good Morning';
      } else if (this.hours < 16) {
        this.msg = 'Good Afternoon';
      } else if (this.hours < 19) {
        this.msg = 'Good Evening';
      } else if (this.hours < 24) {
        this.msg = 'Good Night';
      }
    });

  }
}